      height:329px;
      background:rgba(255,255,255,0.9);
      box-shadow:0px 27px 69px 0px rgba(0,18,96,0.13);
      border-radius:8px;
      text-align: center;
      .login-title{
        text-align: center;
        font-size: 28px;
        font-weight: 500;
        height: 60px;
        line-height: 60px;
        background-color: #4488c0;
        color: #fff;
      }
      .contentLogin{
            display: flex;
            justify-content: space-between;
            border-top: 1px solid #BABDD8;
	        	.leftLogin{
	        		img{
	        			margin-top:58px;
	        			margin-left: 38px;
	        			border: 1px solid #2B92C2;
	        			padding: 20px;
	        			width:100px;
	        			height:100px;
	        		}
            }
            .rightLogin{
